import React from 'react';
import axios from 'axios';
import '../../styles/add_articulos_form.css';

class AddArticulosForm extends React.Component{
    constructor(props){
        super(props);
        this.state={
            nombrearticulo: "",
            descripcion: "",
            cantidad: 0,
            costo: 0.0
        }
    };

    inputHanddler = event =>{
        event.preventDefault();
        this.setState({[event.target.name]: event.target.value});
    };

    handdleSubmit = event =>{
        event.preventDefault();
        let articulo = {
            nombrearticulo: this.state.nombrearticulo,
            descripcion: this.state.descripcion,
            cantidad: this.state.cantidad,
            costo: this.state.costo,
            eliminado: false
        }
        axios({
            method: "POST",
            url: 'http://localhost:8000/api/articulo/',
            data: articulo,
            responseType: 'json',
        }).then(responce =>{
            console.log(responce);
        });
    };

    render(){
        return <div className="row">
            <form className="add-article-form" onSubmit={this.handdleSubmit} method="POST">
                <div className="form-group">
                    <label htmlFor="articulo-name">Nombre del artículo</label>
                    <input 
                        type="text" 
                        className="form-control" 
                        id="articulo-name" 
                        name="nombrearticulo"
                        value={this.state.nombrearticulo}
                        onChange={event =>this.inputHanddler(event)}
                        placeholder="Nombre del artículo"/>
                </div>
                <div className="form-group">
                    <label htmlFor="articulo-price">Precio del artículo</label>
                    <input 
                        type="text" 
                        className="form-control" 
                        id="articulo-price" 
                        name="costo"
                        value={this.state.costo}
                        onChange={event =>this.inputHanddler(event)}
                        placeholder="Precio del artículo"/>
                </div>
                <div className="form-group">
                    <label htmlFor="articulo-quantity">Cantidad de artículos</label>
                    <input 
                        type="text" 
                        className="form-control" 
                        id="articulo-quantity"
                        name="cantidad"
                        value={this.state.cantidad}
                        onChange={event =>this.inputHanddler(event)}
                        placeholder="Cantidad de artículos"/>
                </div>
                <div className="form-group">
                    <label htmlFor="articuloDescripcion">Descripción del artículo</label>
                    <textarea 
                        className="form-control no-resize" 
                        id="articuloDescripcion" 
                        name="descripcion"
                        value={this.state.descripcion}
                        onChange={event =>this.inputHanddler(event)}
                        rows="3"></textarea>
                </div>
                <div className="form-group">
                    <label htmlFor="exampleFormControlFile1">Example file input</label>
                    <input type="file" multiple className="form-control-file" id="exampleFormControlFile1"/>
                </div>
                <button type="submit" className="btn btn-light">Submit</button>
            </form>
        </div>
    };
}

export default AddArticulosForm;